body {
  margin: 0;
  font-family: "Work Sans", sans-serif;
  background-color: antiquewhite;
}

.navbar {
  position: sticky;
  top: 0;
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

.navbar-list {
  float: left;
}

.navbar-list a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.navbar-list a:hover {
  background-color: #111;
}

.navbar-list.cart-display {
  float: right;
}

.store-container {
  display: inline-flex;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.store {
  display: grid;
  max-width: 80%;
  grid-template-columns: 33% 33% 33%;
  grid-row-gap: 20px;
  grid-column-gap: 20px;
}

.product {
  border-radius: 10%;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product.info {
  padding: 10px;
  background-color: white;
  display: inline-block;
}

.img {
  border-radius: 10%;
  max-width: 100%;
}

.add-to-cart-button {
  bottom: 0;
  border: none;
  border-radius: 0%;
  background: #15f70d;
  color: rgb(0, 0, 0);
  height: 70px;
  Width: 100%;
  display: inline-block;
  cursor: pointer;
  text-transform: uppercase;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.add-to-cart-button:active {
  background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.cart-container {
  display: block;
  padding: 10%;
}

.cart {
  display: inline;
  width: 70%;
}

.cart-item {
  display: grid;
  grid-template-columns: 10% 80% 10%;
  grid-row-gap: 20px;
  grid-column-gap: 20px;
  width: 100%;
}

.cart-image {
  width: 100px;
}

.remove-item-button {
  bottom: 0;
  border: none;
  border-radius: 0%;
  background: #666565;
  color: rgb(255, 255, 255);
  padding: 10px;
  cursor: pointer;
  text-transform: uppercase;
}

.remove-item-button:active {
  background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}


.cart-item-price {
  float: right;
}

.submit-order-button {
  bottom: 0;
  border: none;
  border-radius: 0%;
  background: #15f70d;
  color: rgb(0, 0, 0);
  padding: 20px;
  cursor: pointer;
  text-transform: uppercase;
}

.submit-order-button:active {
  background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.cart-total {
  float: right;
}

.thank-you-msg {
  height: 75vh;
  text-align: center;
}

.footer {
  height: 200px;
  background-color: #111;
  width: 100%;
}